@import '../../variables.scss';

.home-page {
    @include margin-60;
    @include mobile {
        margin: 10px;
    }
    @include tablette {
        margin: 20px;
    }

    .home-banner {
        position: relative;
        .home-banner-img {
            width: 100%;
            height: 220px;
            object-fit: cover;
            border-radius: 25px;
            filter: brightness(50%);
            box-shadow: rgba(0, 0, 0, 0.15) 0 5px 2.6px;
            @include mobile {
                height: 110px;
            }
            @include tablette {
                height: 200px;
            }
        }
        .home-banner-title {
            color: white;
            font-size: 45px;
            text-align: center;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            @include mobile {
                font-size: 24px;
                text-align: left;
                transform: translate(-45%, -50%);
                white-space: nowrap;
                width: 95%;
            }
            @include tablette {
                font-size: 34px;
                white-space: wrap;
            }
            .mobile-only {
                display: none;
                @include mobile{
                    display: block;
                }
            }
        }
    }
    
    .home-cards-container {
        margin-top: 25px;
        background-color: rgb(230, 230, 230);
        border-radius: 25px;
        padding: 30px 20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        gap: 20px;
        @include mobile {
            background-color: transparent;
            padding: 0;
            flex-direction: column;
            gap: 15px;
            margin-top: 15px;
        }
        @include tablette {
            background-color: transparent;
            padding: 0;
        }
    }
}