@import '../../variables.scss';

footer {
    background-color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 20px;
    height: 110px;
    width: 100%;
    margin-top: 25px;
    @include mobile {
        height: 200px;
        padding: 0;
    }

    .footer-logo {
        width: 122px;
        @include mobile {
            padding-bottom: 20px;
        }
    }
    
    p {
        padding-top: 20px;
        color: white;
        @include mobile {
            padding: 0 130px;
            text-align: center;
        }
    }
}

